@-webkit-keyframes transition-bg {
  0% {
    background: #e9defa; }
  50% {
    background: #fbfcdb; }
  100% {
    background: #e9defa; } }

@keyframes transition-bg {
  0% {
    background: #e9defa; }
  50% {
    background: #fbfcdb; }
  100% {
