.app {
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }
  .app .content-right {
    text-align: end; }
  .app h1 {
    padding-left: 4em; }
  .app .board {
    display: flex;
    flex-direction: row;
    align-items: flex-start; }
    .app .board > .info {
      font-size: 1.2em;
      width: 14em;
      margin: 0 1em; }
      .app .board > .info > .line {
        height: 2.5em;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid #bebebe; }
        .app .board > .info > .line > .title {
          width: 8em;
