          cursor: default; }
        .app .board > .info > .line select {
          width: 6em;
          height: 2em;
          border-radius: 0.25em;
          font-size: 1.1em;
          -webkit-appearance: none;
          padding-left: 3em;
          background: transparent; }
        .app .board > .info > .line select:focus {
          outline: none; }
        .app .board > .info > .line button {
          width: 5em;
          border-radius: 0.25em;
          height: 2em;
          background: #bebebe;
          font-size: 0.9em; }
        .app .board > .info > .line button:focus {
          outline: none; }
        .app .board > .info > .line .restart {
