          border-color: #5bc0de; }
        .app .board > .info > .line .animation {
          background: #FA8072;
          border-color: #FA8072; }
    .app .board > .zone > .matrix {
      display: flex;
      flex-direction: column; }
      .app .board > .zone > .matrix > .row {
        display: flex;
        flex-direction: row;
        border-top: 1px solid black; }
        .app .board > .zone > .matrix > .row > .cell {
