            transition: all ease 0.5s;
            background: #98c9f5; }
          .app .board > .zone > .matrix > .row > .cell > .revealed-block {
            width: 2em;
            height: 2em;
            text-align: center;
            line-height: 2em;
            -webkit-transition: all ease 0.5s;
            transition: all ease 0.5s;
            background: transparent; }
        .app .board > .zone > .matrix > .row > .cell:last-child {
