      justify-content: flex-start;
      align-items: flex-start;
      font-weight: 500;
      margin-left: 1em; }
      .app .board > section > .line {
        margin: 1em 0; }
      .app .board > section > .line:first-child {
        margin: 0;
        font-size: 1.4em; }
